
import React, { Component } from 'react';
import FakeImg from './component/FakeImg';
import Highlight from 'react-highlight'

import './bulma.min.css';
import './atelier-estuary-light.css';
import './App.css';

class App extends Component {

  renderHeader = () => {
    return (
      <header>
        <div className="container has-text-centered">
          <h1 className="title is-1">React FakeImg</h1>
          <p style={{ marginBottom: '20px' }}>
            <a
              className="github-button"
              href="https://github.com/HuuDuc/react-fakeimg"
              data-show-count="true"
              aria-label="Star HuuDuc/react-fakeimg on GitHub"
            >
              Star
            </a>
            <a
              className="github-button"
              href="https://github.com/HuuDuc/react-fakeimg/fork"
              data-show-count="true"
              aria-label="Fork HuuDuc/react-fakeimg on GitHub"
            >
              Fork
            </a>
          </p>
          <p>
            Your designer team didn't provide you an image file to put in your project ?
            You need to make a mockup but you don't have images ? <br />That's fine you can use
            this component until you get the file. <strong>react-fakeimg</strong> will
            create for you a fake image powered by <strong>canvas</strong>. <br />There is no <strong>http request </strong>
            so you can still use it even you don't have an <strong>internet connection</strong>.
          </p>
        </div>
      </header>
    );
  }

  renderFooter = () => {
    return (
      <footer>
        <div className="container has-text-centered">
          <h1 className="title is-1">React FakeImg</h1>
          <p>
            Made by <a href="http://www.stephanenguyen.fr"><strong>Stephane Nguyen</strong></a>. <strong>"react-fakeimg"</strong> is freely distributable under the terms of the MIT license.
          </p>
        </div>
      </footer>
    );
  }

  renderHowToInstall = () => {
    return (
      <section style={{ padding: '30px 0' }}>
        <div className="container">
          <h3 className="title is-3">Installation</h3>
          <h4 className="title is-4">via npm</h4>
          <Highlight className='bash'>
            {'npm install react-fakeimg'}
          </Highlight>
          <h4 className="title is-4">via yarn</h4>
          <Highlight className='bash'>
            {'yarn add react-fakeimg'}
          </Highlight>
        </div>
      </section>
    );
  }

  renderHowToUse = () => {
    return (
      <section style={{ padding: '30px 0' }}>
        <div className="container">
          <h3 className="title is-3">How to use (ES6)</h3>
          <div className="columns">
            <div className="column">
              <h4 className="title is-4">Simple example</h4>
              <div style={{ height: '180px', padding: '10px 0' }}>
                <FakeImg
                  height={150}
                  width={150}
                  alt="square"
                />
              </div>
              <Highlight className='js'>
                {`<FakeImg
  height={150}
  width={150}
  alt="square"
/>`}
              </Highlight>
            </div>
            <div className="column">
              <h4 className="title is-4">Text example</h4>
              <div style={{ height: '180px', padding: '10px 0' }}>
                <FakeImg
                  height={50}
                  width={210}
                  alt="square"
                  text="Bonjour"
                />
              </div>
              <Highlight className='js'>
                {`<FakeImg
  height={50}
  width={210}
  alt="text"
  text="Bonjour"
/>`}
              </Highlight>
            </div>
            <div className="column">
              <h4 className="title is-4">Color example</h4>
              <div style={{ height: '180px', padding: '10px 0' }}>
                <FakeImg
                  height={100}
                  width={200}
                  alt="color"
                  bgColor="#027b76"
                  fontColor="#ffffff"
                />
              </div>
              <Highlight className='js'>
                {`<FakeImg
  height={100}
  width={200}
  alt="color"
  bgColor="#027b76"
  fontColor="#ffffff"
/>`}
              </Highlight>
            </div>
          </div>
        </div>
      </section>
    );
  }

  renderApi = () => {

    const data = [
      {
        name: 'alt',
        type: 'string',
        required: true,
        default: null,
        description: 'alt attribute is require in HTML5 for img tag',
      },
      {
        name: 'bgColor',
        type: 'string',
        required: false,
        default: '#c19d67',
        description: 'Background colors',
      },
      {
        name: 'className',
        type: 'string',
        required: false,
        default: null,
        description: 'Add class attribute',
      },
      {
        name: 'fontColor',
        type: 'string',
        required: false,
        default: '#3a2306',
        description: 'Font color',
      },
      {
        name: 'height',
        type: 'Number',
        required: true,
        default: null,
        description: 'Image height',
      },
      {
        name: 'id',
        type: 'string',
        required: false,
        default: null,
        description: '#id',
      },
      {
        name: 'text',
        type: 'string',
        required: false,
        default: null,
        description: 'text to replace "width x height"',
      },
      {
        name: 'style',
        type: 'object',
        required: false,
        default: null,
        description: 'If you want to add style',
      },
      {
        name: 'width',
        type: 'Number',
        required: true,
        default: null,
        description: 'Image width',
      }
    ];

    const dataMethods = [
      {
        name :'onClick',
        type: 'func',
        parameters: 'none',
        required: false,
        description: 'Called when click on the component'
      },
      {
        name :'onMouseEnter',
        type: 'func',
        parameters: 'none',
        required: false,
        description: 'Called when mouse enter the component'
      },
      {
        name :'onMouseLeave',
        type: 'func',
        parameters: 'none',
        required: false,
        description: 'Called when mouse leave the component'
      },
    ];

    return (
      <div className="container" style={{ padding: '30px 0' }}>
        <h3 className="title is-3">API</h3>
        <h4 className="title is-4">Attributes list</h4>
        <table className="table is-striped">
          <thead>
            <tr>
              <th>Properties</th>
              <th>type</th>
              <th>Required</th>
              <th>default</th>
              <th>Description</th>
            </tr>
          </thead>
          <tbody>
            {
              data.map((line, i) => {
                return (
                  <tr kay={`line${i}`}>
                    <th>{ line.name }</th>
                    <td>{ line.type }</td>
                    <td>{ line.required && '✓' }</td>
                    <td>{ line.default }</td>
                    <td>{ line.description }</td>
                  </tr>
                )
              })
            }
          </tbody>
        </table>

        <h4 className="title is-4">Events list</h4>
        <table className="table is-striped">
          <thead>
            <tr>
              <th>Name</th>
              <th>type</th>
              <th>Parameters</th>
              <th>Required</th>
              <th>Description</th>
            </tr>
          </thead>
          <tbody>
            {
              dataMethods.map((line, i) => {
                return (
                  <tr kay={`line${i}`}>
                    <th>{ line.name }</th>
                    <td>{ line.type }</td>
                    <td>{ line.parameters }</td>
                    <td>{ line.required && '✓' }</td>
                    <td>{ line.description }</td>
                  </tr>
                )
              })
            }
          </tbody>
        </table>
      </div>
    );
  }

  render() {
    return (
      <div>
        {this.renderHeader()}
        {this.renderHowToInstall()}
        {this.renderHowToUse()}
        {this.renderApi()}
        {this.renderFooter()}
      </div>
    );
  }
}

export default App;



